/* src/styles/Desktop.css */

/* --- 1. Main Container --- */
.desktop-container {
  width: 100vw;
  height: 100vh;
  position: relative;
  overflow: hidden;
  /* Fallback color if wallpaper fails */
  background-color: #000;
  font-family: 'Segoe UI', sans-serif;
  user-select: none;
}

/* --- 2. Wallpaper Layer --- */
.wallpaper-layer {
  position: absolute;
  inset: 0;
  z-index: 0;
  
  /* CHANGE 1: Force fit to screen (Stretch) */
  background-size: 70% 100%; 
  background-repeat: no-repeat;
  background-position: center;

  /* Optional: Thoda dark overlay taaki icons clear dikhe */
  background-color: #000; 
  transition: all 0.5s ease;
}

/* --- Text Alignment Fix (Naam ko neeche laana hai) --- */
.hero-text-container {
  position: absolute;
  /* CHANGE 2: Text ko neeche shift kiya taaki Mask ka face dikhe */
  top: 80%; 
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  pointer-events: none;
  text-align: center;
  width: 100%;
}

.hero-name {
  /* CHANGE 3: Font size thoda adjust kiya */
  font-size: 6rem; 
  font-weight: 800;
  text-transform: capitalize;
  
  /* Outline Style */
  color: transparent;
  -webkit-text-stroke: 1px rgba(255, 255, 255, 0.3);
  
  letter-spacing: 4px;
  font-family: 'Segoe UI', sans-serif;
  
  /* Thoda Glow effect for Cyberpunk feel */
  filter: drop-shadow(0 0 10px rgba(0,0,0,0.8));
}
/* --- 4. Desktop Icons Grid (Left Side) --- */
.icons-grid {
  position: absolute;
  top: 20px;
  left: 20px;
  display: flex;
  flex-direction: column; /* Vertical List */
  gap: 25px; /* Spacing between icons */
  z-index: 10;
}

.desktop-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 90px;
  cursor: pointer;
  padding: 5px;
  border-radius: 4px;
  transition: background 0.2s;
}

.desktop-icon:hover {
  background-color: rgba(255, 255, 255, 0.1); /* Windows selection highlight */
}

.icon-img {
  width: 48px;
  height: 48px;
  margin-bottom: 5px;
  /* Drop shadow for icons to pop against wallpaper */
  filter: drop-shadow(0 2px 4px rgba(0,0,0,0.5)); 
}

.icon-label {
  color: white;
  font-size: 13px;
  text-align: center;
  text-shadow: 0 1px 3px rgba(0,0,0,0.9);
  line-height: 1.2;
}